.app {
  background-color: #FCC13F;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif; }
  .app .login-view {
    padding-top: 20%;
    text-align: center; }
    .app .login-view .error {
      border: 1px solid #6E0E0A;
      color: #6E0E0A;
      border-radius: 5px;
      margin-left: 5px;
      padding: 10px; }

.panel {
  background-color: #FEFEFE;
  display: inline-block;
  border-radius: 5px;
  padding: 10px;
  font-size: 14px; }

.form-input {
  text-align: left;
  margin-bottom: 15px; }
  .form-input span {
    font-size: 12px; }
  .form-input input {
    font-size: 16px;
    display: block;
    border: none;
    border-bottom: 1px dotted black; }
    .form-input input:focus {
      outline: none; }

button {
  border: 1px dotted black;
  padding: 10px;
  border-radius: 5px;
  font-size: 14px; }
  button:focus {
    background-color: #CECECE;
    outline: none; }
